	/**
	 * 解析可见性命令
	 * @param command
	 * @param cmdParams
	 * @param targetComp
	 * @param autoReverse
	 */
	function parseVisibleCmd(command, cmdParams, targetComp, autoReverse) {
	  var value = findEnumValue(EnumData.CommandVisibility, cmdParams[EnumData.CommandParams.PARAM_VALUE]);
	  var currentOpacity; // 组件的原始透明度
	  var toOpacity; // 组件的目标透明度
	  var toVisible;
	
	  //当前是否可见
	  var currentVisible = !_.isUndefined(targetComp.currentVisible) ? targetComp.currentVisible : targetComp.isShow; // 组件当前是否可见
	  currentOpacity = parseFloat(targetComp.opacity);
	
	  switch (value) {
	    case EnumData.CommandVisibility.SHOW:
	      toVisible = true;
	      break;
	    case EnumData.CommandVisibility.HIDE:
	      toVisible = false;
	      break;
	    default:
	      toVisible = !currentVisible;
	      break;
	  }
	  toOpacity = toVisible ? currentOpacity : 0;
	
	  command.params = {
	    opacity: toOpacity,
	    afterCSS: {
	      visibility: toVisible ? 'visible' : 'hidden',
	      display: toVisible ? 'block' : 'none'
	    }
	  };
	  //只有在即将显示的时候，才把目标预览作隐藏处理
	  if (toVisible) {
	    command.previourCSS = {
	      visibility: toVisible ? 'visible' : 'hidden',
	      display: toVisible ? 'block' : 'none',
	      opacity: 0
	    };
	  }
	  command.recovery = {
	    opacity: toVisible ? 0 : currentOpacity
	  };
	  command.recoveryCSS = {
	    visibility: currentVisible ? 'visible' : 'hidden',
	    opacity: currentVisible ? currentOpacity : 0
	  };
	
	  if (!autoReverse) {
	    targetComp.currentVisible = toVisible;
	  }
	
	  if (toVisible) doUpdateChildVisible(targetComp, toVisible);
	}
	
	function doUpdateChildVisible(groupCom, visible) {
	  var item;
	  var i = 0;
	  if (groupCom.items) {
	    for (i; i < groupCom.items.length; i++) {
	      item = groupCom.items[i];
	      item.isShow = visible;
	      if (item.items) {
	        doUpdateChildVisible(item, visible);
	      }
	    }
	  }
	}
	
