'use strict';

var React = require('react');
var appTypes = require('../../types/appTypes');
var styleHelper = require('../../helper/styleHelper');
var propConst = require('../../constants/propConst');

/**
 * 水平滑块
 */
var VSlider = React.createClass({

  render: function () {
    var trackStyle;
    var sliderStyle;
    var progress = styleHelper.getInt(this.props, propConst.PROGRESS);
    var w = styleHelper.getWidth(this.props);
    var h = styleHelper.getHeight(this.props);
    var state = styleHelper.getPropertyValue(this.props, propConst.STATUS);
    if(!state){
      state = this.props[propConst.STATE];
    }
    var bg = state === appTypes.ComStatus2.DISABLE ? appTypes.Color.MODEL_DISABLE : '#000';

    trackStyle = {
      left: (w - 2) / 2,
      backgroundColor: this.props.borderColor
    };
    
    sliderStyle = {
      top: (progress / 100) * h
    };


    return (<div>
      <div className="track" style={trackStyle}></div>
      <div className="slide" style={sliderStyle}>
        <div className="slide-shape" style={{borderLeftColor: this.props.borderColor}}></div>
      </div>
    </div>)
  }

});

module.exports = VSlider;



